/* Product Details Page */
.productpage-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
  background-color: #ffffff;
}

/* Top Bar */
.productpage-top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #e2e8f0;
}

.productpage-back-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background-color: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  color: #4a5568;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.productpage-back-btn:hover {
  background-color: #edf2f7;
  border-color: #cbd5e0;
  transform: translateY(-1px);
}

.productpage-quote-btn {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  border-radius: 8px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 4px rgba(102, 126, 234, 0.2);
}

.productpage-quote-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(102, 126, 234, 0.3);
}

.productpage-quote-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* Main Content Layout */
.productpage-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: start;
}

/* Image Section */
.productpage-image-wrapper {
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  background-color: #f8fafc;
}

.productpage-image {
  width: 100%;
  height: 500px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.productpage-image:hover {
  transform: scale(1.02);
}

/* Product Info Section */
.productpage-info {
  padding: 1rem 0;
}

.productpage-name {
  font-size: 2.25rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 0.5rem;
  line-height: 1.2;
  font-family: 'Inter', sans-serif;
}

.productpage-category {
  display: inline-block;
  padding: 0.25rem 0.75rem;
  background-color: #ebf8ff;
  color: #2b6cb0;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 1rem;
}

.productpage-price {
  font-size: 2rem;
  font-weight: 700;
  color: #2d3748;
  margin-bottom: 1.5rem;
  font-family: 'Inter', sans-serif;
}

.productpage-description {
  font-size: 1.125rem;
  line-height: 1.7;
  color: #4a5568;
  margin-bottom: 2rem;
  padding: 1.5rem;
  background-color: #f7fafc;
  border-radius: 8px;
  border-left: 4px solid #4299e1;
}

/* Quantity Controls */
.productpage-quantity-section {
  margin-bottom: 2rem;
}

.productpage-quantity-label {
  display: block;
  font-weight: 600;
  color: #2d3748;
  margin-bottom: 0.75rem;
  font-size: 1rem;
}

.productpage-quantity-row {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  max-width: 200px;
}

.productpage-qty-btn {
  width: 44px;
  height: 44px;
  border: 1px solid #e2e8f0;
  background-color: white;
  border-radius: 8px;
  font-size: 1.25rem;
  font-weight: 600;
  color: #4a5568;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.productpage-qty-btn:hover:not(:disabled) {
  background-color: #f7fafc;
  border-color: #cbd5e0;
  transform: translateY(-1px);
}

.productpage-qty-btn:active {
  transform: translateY(0);
}

.productpage-qty-input {
  width: 80px;
  height: 44px;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  text-align: center;
  font-size: 1.125rem;
  font-weight: 600;
  color: #2d3748;
  background-color: white;
}

.productpage-qty-input:focus {
  outline: none;
  border-color: #4299e1;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.1);
}

/* Add to Cart Button */
.productpage-add-to-cart-btn {
  width: 100%;
  padding: 1.25rem 2rem;
  background: linear-gradient(135deg, #48bb78 0%, #38a169 100%);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 1.125rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(72, 187, 120, 0.3);
  margin-top: 1rem;
}

.productpage-add-to-cart-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(72, 187, 120, 0.4);
}

.productpage-add-to-cart-btn:active {
  transform: translateY(0);
}

.productpage-add-to-cart-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  .productpage-container {
    padding: 0.5rem;
  }

  .productpage-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .productpage-top-bar {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .productpage-back-btn {
    justify-content: center;
  }

  .productpage-image {
    height: 400px;
  }

  .productpage-name {
    font-size: 1.75rem;
  }

  .productpage-price {
    font-size: 1.75rem;
  }

  .productpage-description {
    font-size: 1rem;
    padding: 1rem;
  }

  .productpage-add-to-cart-btn {
    padding: 1rem 1.5rem;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .productpage-image {
    height: 300px;
  }

  .productpage-name {
    font-size: 1.5rem;
  }

  .productpage-price {
    font-size: 1.5rem;
  }

  .productpage-quantity-row {
    max-width: 100%;
    justify-content: center;
  }

  .productpage-qty-input {
    width: 100px;
  }
}

/* Loading States */
.productpage-loading {
  opacity: 0.7;
  pointer-events: none;
}

/* Focus States for Accessibility */
.productpage-back-btn:focus,
.productpage-quote-btn:focus,
.productpage-qty-btn:focus,
.productpage-qty-input:focus,
.productpage-add-to-cart-btn:focus {
  outline: 2px solid #4299e1;
  outline-offset: 2px;
}

/* Animation for smooth transitions */
.productpage-container * {
  transition: all 0.2s ease;
}